.root-results-win {
  min-width: 375px;
  width: 100%;
  height: 100vh;
  background-color: var(--color-ganador-secundario);
  grid-template-rows: 26% 44% 15% 15%;
  align-items: center;
  padding: 30px;
  display: grid;
}

@media (min-width: 450px) {
  .root-results-win {
    height: inherit;
    border: 4px solid #000;
    border-radius: 6px;
    gap: 10px;
    padding: 20px 45px 40px;
  }
}

@media (min-width: 750px) {
  .root-results-win {
    padding: 20px 55px 50px;
  }
}

@media (min-width: 950px) {
  .root-results-win {
    padding: 20px 75px 50px;
  }
}

@media (min-width: 1150px) {
  .root-results-win {
    height: inherit;
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: blur(4px);
    border: 6px solid #000;
    border-radius: 6px;
    grid-template-rows: 26% 44% 15% 15%;
    gap: 12px;
    padding: 0 115px 55px;
  }
}

